@import url('https://fonts.googleapis.com/css2?family=Readex+Pro:wght@200;300;400;500;600;700;800&display=swap');

html, body {
    margin: 0;
    padding: 0;
    text-align: center;
    zoom: 100%;
}

h2 {
    font-style: none;
}

* {
    font-family: 'Readex Pro', sans-serif;
}

.primary-title {
    font-size: 90px;
    color: rgb(255, 255, 255);
}

.primary-button {
    color: rgb(255,255,255);
    text-transform: uppercase;
    font-size: 15px;
    background: linear-gradient(45deg, #8A9A5B, #6082B6);
    padding: 12px 30px;
    border-radius: 30px;
    border: none;
}

.primary-button:hover {
    background: linear-gradient(260deg, #8A9A5B, #6082B6);
}

.secondary-button {
    color: rgb(121, 119, 119);
    background-color: rgb(255, 255, 255);
    font-weight: 500;
    font-size: 15px;
    text-transform: uppercase;
    padding: 12px 30px;
    border-radius: 30px;
    border: solid 2px rgb(121, 119, 119);
    margin: 6px;
}

.secondary-button:hover {
    color: white;
    background-color: rgb(48, 48, 48);;
}


/*-------- Navbar --------*/

nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

nav .nav-button {
    color: white;
    background-color: transparent;
    font-size: 20px;
    padding: 10px 20px;
    border-radius: 10px;
    border: none;
    margin: 15px;
    height: fit-content;
}

nav .nav-button:hover {
    color: lightgray;
}

nav .nav-button:disabled {
    background-color: transparent;
    color: darkgray;
}

nav .logo-container {
    width: 170px;
    margin-top: 15px;
    margin-left: -30px
}

nav .logo-container .logo {
    width: 30%;
}

@media screen and (max-width: 414px) {
    nav .logo-container {
        width: 170px;
        margin-top: 10px;
        margin-left: -40px
    }
}

/*-------- Home page --------*/



.overlay {
    width: 100vw;
    height: 100vh;
    position: fixed;
    background: linear-gradient(
            to bottom,
            rgba(0, 0, 0, 0.65) 0%,
            rgba(0, 0, 0, 0) 100%
    ), url('https://i.imgur.com/AIoIit0.png');
    background-position: center;
    margin: 0px;
    padding: 0px;
    -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.home {
    margin-top: 30vh;
}

.home h1 {
    color: white;
}

/*------- Mobile Section First -------*/
@media screen and (max-width: 414px) {
    .overlay {
    height: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    }
  }

/*------- End Mobile Section ------ */

/*-------- Auth Modal --------*/
.auth-modal {
    position: absolute;
    left: 0;
    right: 0;
    top: 90px;
    margin-left: auto;
    margin-right: auto;
    max-width: 360px;
    height: 510px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    padding: 40px;
}

.auth-modal .close-icon {
    float: right;
}

.auth-modal form {
    display: flex;
    flex-direction: column;
}

.auth-modal a{
    text-decoration:none;
}

.auth-modal input[type=password],
.auth-modal input[type=email],
.auth-modal input[type=password-check] {
    padding: 8px;
    margin: 8px;
    font-size: 17px;
    border-radius: 5px;
    border-width: 2px;
    border-color: inherit;
}

div.close-icon::before {
    content: url(images/close.png);
    size: 10px;
    
}

@media screen and (max-width: 414px) {
    .auth-modal {
        height: 75%;
        width: 65%;
        top: 50px;
    }

    div.close-icon::before {
        content: url(images/close.png);
        size: 5px;
        
    }

    .auth-modal input[type=password],
    .auth-modal input[type=email],
    .auth-modal input[type=password-check] {
    border-width: 1px;
    border-color: rgb(118, 118, 118);
}
}

/*-------- OnBoarding Page --------*/
.onboarding {
    border-top: solid 1px rgb(213, 213, 213);
}

.onboarding form {
    display: flex;
    justify-content: center;
}

.onboarding form section {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 35%;
    text-align: start;
}

.onboarding form input {
    padding: 15px 30px;
    margin: 10px 0;
    font-size: 15px;
    border: solid 2px rgb(219, 219, 219);
    border-radius: 10px;
}

.onboarding form label {
    margin: 10px 0;
}

.onboarding form .multiple-input-container {
    display: flex;
    flex-direction: row;
}

.onboarding form input::-webkit-outer-spin-button,
.onboarding form input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.onboarding form .multiple-input-container input {
    margin-right: 10px;
}

.onboarding form .multiple-input-container label {
    padding: 10px;
    border: solid 2px rgb(144, 144, 144);
    border-radius: 10px;
    transition: all 0.3s;
    margin-right: 10px;
}

.onboarding form .colleges {
    display: flex;
    flex-direction: row;
    max-width: 100px;
}

.onboarding form .colleges input {
    margin-right: 10px;
    max-width: 100px;
}

.onboarding form .colleges label {
    padding: 10px;
    border: solid 2px rgb(144, 144, 144);
    border-radius: 10px;
    transition: all 0.3s;
    margin-right: 10px;
    max-width: 100px;
}

.onboarding form input[type=radio] {
    display: none;
}

.onboarding form input[type=number] {
    width: 10%;
}

.onboarding form input[type=radio]:checked + label {
    border: solid 2px #6082B6;
}

.onboarding form input[type=submit]:hover {
    background-color: rgb(235, 235, 235);
}

.onboarding form input[type=submit]:active {
    background-color: rgb(226, 115, 115);
}

.onboarding form .photo-container img {
    width: 100%
}

.room-with-desc a{
    font-size: 12px;
}

.gender-yes-no {
    margin-right: 50px !important;
}

@media screen and (max-width: 414px) {
    .onboarding form section {
        width: 99%;
        padding-right: 5%;
    }
}



/*-------- Dashboard  --------*/
.dashboard {
    display: flex;
    justify-content: space-between;
    background-color: #FAFBFE  ;
}

.dashboard .swipe-container {
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.swipe {
    position: absolute;
}

.dashboard .card-container {
    width: 400px;
    height: 650px;
    margin-top: 15vh;
    margin-right: 35vh;
    margin-left: 35vh;
}

.card {
    width: 45vh;
    height: 70vh;
    border-radius: 30px;
    background-size: cover;
    background-position: center;
    box-shadow: 5px 5px 60px 5px rgb(0, 0,0, 0.03);
    background-color: white;
}

.card h1 {
    padding-top: 45px;
    padding-bottom: 25px;
    font-weight: bold;
    color: #333333;
}

.dashboard .swipe-info {
    position: absolute;
    bottom: 0;
    padding: 10px;
}

/*-------- Chat Container --------*/
.chat-container {
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    width: 30%;
    text-align: left;
    z-index: 1;
    height: 100vh;
}

.chat-container .chat-container-header {
    background: linear-gradient(45deg, #6082B6, #8A9A5B);
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.chat-container .option {
    border: none;
    background-color: rgb(255, 255, 255);
    border-bottom: solid 4px #6082B6;
    font-size: 20px;
    margin: 2px;
    padding: 10px;
}

.chat-container .option:disabled {
    border-bottom: solid 4px rgb(187, 187, 187);
}

.chat-container .profile,
.chat-container .log-out-icon {
    display: flex;
    align-items: center;
    padding: 20px;
    color: rgb(255, 255, 255);
}

.img-container {
    height: 30px;
    width: 30px;
    border-radius: 15px;
    overflow: hidden;
    margin: 10px;
}

.img-container img {
    width: 100%;
}

.match-card {
    border-style: solid;
    border-width: .5px;
    border-radius: 10px;
    margin-bottom: 5px;
    padding-left: 15px;
    border-color: lightgray;
}

.match-card h3 {
    font-weight: 400;
    color: #333333;
}

/*-------- Chat Display --------*/
.chat-display,
.matches-display {
    padding: 20px;
    height: 60vh;
    overflow-y: auto;
}

/*-------- Chat Input --------*/
.chat-input {
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.chat-input textarea{
    border-radius: 10px;
    padding-bottom: 10px;
}


@media screen and (max-width: 1270px) {
    .dashboard .card-container {
        margin-left: 30vh;
        margin-right: 30vh;
    }

    .chat-container {
        width: 40%;
    }
}


@media screen and (max-width: 1175px) {
    .dashboard .card-container {
        margin-left: 25vh;
        margin-right: 25vh;
    }

    .chat-container {
        width: 50%;
    }
}

@media screen and (max-width: 1082px) {
    .dashboard .card-container {
        margin-left: 20vh;
        margin-right: 20vh;
    }

    .chat-container {
        width: 60%;
    }
}

@media screen and (max-width: 988px) {
    .dashboard .card-container {
        margin-left: 15vh;
        margin-right: 15vh;
    }

    .chat-container {
        width: 70%;
    }
}

@media screen and (max-width: 894px) {
    .dashboard .card-container {
        margin-left: 10vh;
        margin-right: 10vh;
    }

    .chat-container {
        width: 80%;
    }
}

@media screen and (max-width: 800px) {
    .dashboard .card-container {
        margin-left: 5vh;
        margin-right: 5vh;
    }

    .chat-container {
        width: 90%;
    }
}

@media screen and (max-width: 600px) {
    .dashboard .card-container {
        max-width: 100px;
        margin-right: 150vh;
        margin-left: 2vh;
    }
    .chat-container {
        width: 30%;
    }
}

@media screen and (max-width: 510px) {
    .dashboard .card-container {
        max-width: 100px;
        margin-right: auto;
        margin-left: auto;
    }
    .chat-container {
        width: 100vh !important;
    }
}

@media screen and (max-width: 450px) {
    .dashboard .card-container {
        max-width: 100px;
        margin-right: auto;
        margin-left: auto;
    }
    .chat-container {
        width: 100vh !important;
    }

    .chat-display {
        height: 40%;
    }

    .chat-input {
        margin-top: 0px;
        background-color: white;
        z-index: 2;
    }

    .dashboard {
        transform: scale(0.6);
        transform-origin: 0 0;
     }
}

@media screen and (max-height: 700px) {
    .card {
        height: 80vh;
        margin-top: -10vh;
        width: 55vh;
    }
}


.chat-message-header p {
    font-weight: bold;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto;
  padding-top: 5px;
  padding-right: 5px;
  padding-left: 5px;
}

.grid-container-2 {
    display: grid;
    grid-template-columns: auto auto;
    padding-top: 5px;
    padding-right: 5px;
    padding-left: 5px;
  }

.grid-container div {
    text-align: center;
}